#intro{
    height:calc(100vh - 4rem);
    width:100vw;
    max-width:65rem;
    margin:0 auto;
    overflow:hidden;
    position:relative;
  }
  
  .bg{
    position: absolute;
    top:-4rem;
    right:0;
    z-index:-1;
    object-fit:cover;
    height: 90vh;
    padding-top: 8rem;
  }
  
  .introContent{
    height:100vh;
    width:100vw;
    padding: 2rem;
    font-size:3rem;
    font-weight:700;
    display: flex;
    flex-direction:column;
    justify-content:center;
  }
  .hello{
    font-size:1.75rem;
    font-weight:100;
    letter-spacing:1px
  }
  .introName{
    color:yellow;
    
  }
  
  .introPara{
    font-size:medium;
    font-weight:300;
    
  }
  
  .btn{
    background:white;
    margin:1rem 0;
    padding:0.75rem 2rem;
    border: none;
    border-radius: 2rem;
  }
  .btnImg{
    object-fit:cover;
    margin: 0 0.25rem;
    height: 1rem;
  }
  
  @media screen and (max-width: 1000px){
    .bg{
      right:-10vw;
    }
  }