.navbar{
    background:rgb(30,30,30);
    height: 5rem;
    width: 100vw;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top:0;
    z-index: 3;
    max-width: 65rem;
  }
  
  .logo{
    object-fit:cover;
    height: 4rem;
    width: 4rem;
    margin-left: 23px;
  }
  
  .desktopMenuListItem{
    margin: 1rem;
    color: white;
    cursor: pointer;
  }
  .desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
  }
  .desktopBtn{
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem
  }
  
  .desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
  }
  .active{
    color: yellow;
    border-bottom: 3px solid yellow;
    padding-bottom:0.5rem;
  }
  .mobMenu{
    display:none;
    object-fit:cover;
    height:2.8rem;
  }
  .navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding:0.5rem;
    height: 0.5rem;
    min-width: 15rem;
    background:rgb(40, 40, 40);
    border-radius: 1rem;
  }
  
  .listItem{
    color: white;
    padding:0.5rem 3rem;
    margin:0.25rem;
    background:rgb(30,30,30);
    
  }
  
  @media screen and (max-width:673px){
    .mobMenu{
      display:flex;
    }
    .desktopMenu{
      display:none;
    }
    .desktopBtn{
      display:none;
    }
  }